            <el-button @click="editAddressInfo" size="mini" type="primary" icon="el-icon-edit"></el-button>
            <el-button @click="logisticsInfo" size="mini" type="success" icon="el-icon-location"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryGoodsOrders.pagenum" :page-sizes="[5, 10, 15]" :page-size="queryGoodsOrders.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total"> </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog @close="addDialogVisibleClose" title="" :visible.sync="addDialogVisible" width="50%">
      <el-form :model="editAddress" :rules="editAddressRules" ref="editAddressRef" label-width="100px">
        <el-form-item label="/" prop="consignee_addr">
          <el-cascader v-model="editAddress.consignee_addr" :options="citydata" :props="{ expandTrigger: 'hover' }"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="consignee_info">
          <el-input v-model="editAddress.consignee_info"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="logisticsVisible" width="50%">
      <el-timeline>
        <el-timeline-item v-for="(item, index) in logistcsList" :key="index" :timestamp="item.time">
          {{ item.context }}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="logisticsVisible = false"> </el-button>
        <el-button type="primary" @click="logisticsVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
