<script>
// 
import citydata from './citydata.js'
export default {
  name: 'Orders',
  data() {
    return {
      // 
      queryGoodsOrders: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      total: 0,
      // 
      goodsOrdersList: [],
      // 
      addDialogVisible: false,
      editAddress: {
        consignee_addr: [],
        consignee_info: ''
      },
      editAddressRules: {
        consignee_addr: [{ required: true, message: '/', trigger: 'blur' }],
        consignee_info: [{ required: true, message: '/', trigger: 'blur' }]
      },
      citydata,
      logisticsVisible: false,
      // 
      logistcsList: {}
    }
  },
  created() {
    this.getGoodsOrders()
  },
  methods: {
    async getGoodsOrders() {
      const { data: res } = await this.$http.get('/orders', { params: this.queryGoodsOrders })
      if (res.meta.status !== 200) {
        return this.$message.error(res.meta.msg)
      }
      console.log(res)
      this.goodsOrdersList = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange(newSize) {
      this.queryGoodsOrders.pagesize = newSize
      this.getGoodsOrders()
    },
    handleCurrentChange(newCurrent) {
      this.queryGoodsOrders.pagenum = newCurrent
      this.getGoodsOrders()
    },
    editAddressInfo() {
      this.addDialogVisible = true
    },
    // 
    addDialogVisibleClose() {
      this.$refs.editAddressRef.resetFields()
    },
    // 
